body
  font: 18px/1.8 "Avenir Next"
  width: 30%
  margin: 0 auto

h1
  color: orangered
  font-size: 1.5em
  text-transform: uppercase
  text-align: center

label
  display: block
    
input, textarea
  display: block
  width: 100%
  padding: .5em 0
  font-size: 1em
  border: 1px dashed darkgrey

input
  margin-bottom: 1.5em

  &[type='submit']
    border: none
    font-size: 1em
    padding: 1em 0
    width: 200px
    margin: 1em 0
    color: white
    background-color: orangered
    float: right